.hidden {
  display: none;
}

.visible {
  display: block;
}

.loader {
  width: 64px;
  height: 64px;
  background-image: linear-gradient(#fff 16px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 16px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 16px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 16px, rgba(0, 0, 0, 0) 0);
  background-position: 0 0, 0 100%, 100% 0, 100% 100%;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  animation: rotate 1s linear infinite;
  position: relative;
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.cat-info {
  height: 100%;
  width: auto;
  object-fit: cover;
  text-decoration: none;
}

.cat-info img {
  max-width: 500px;
}

@keyframes rotate {
  0% {
    width: 64px;
    height: 64px;
    transform: rotate(0deg);
  }

  50% {
    width: 30px;
    height: 30px;
    transform: rotate(180deg);
  }

  100% {
    width: 64px;
    height: 64px;
    transform: rotate(360deg);
  }
}

